import service from "../utils/request";
import qs from 'qs';

export function FTP(host){
    return service({
        url: '/ftpcheck',
        method: 'post',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: qs.stringify({
            host: host
        })
    })
}


