<template>

  <div>
  <el-row>
    <img class="logo" style="width: 150px; height: 150px" src="../assets/1.jpg" alt=""/>
    <div class="h1">FTP CHECKER</div>
  </el-row>
  <el-row type="flex" justify="center">
    <el-col :span="40">
      <el-row gutter="20" type="flex" class="row-bg">
        <div>
          <el-input   v-model="form.input" style="width:400px" placeholder="Host" clearable></el-input>
          <el-button type="danger" class="n" @click="Submit">Submit</el-button>
        </div>
      </el-row>
      <el-row gutter="20" type="flex" class="row-bg">
        <el-input
          style="width:500px"
          type="textarea"
          :rows="10"
          placeholder="Result"
          v-model="result"
        >
        </el-input>
      </el-row>
    </el-col>
  </el-row>
  </div>
</template>

<script>
import {FTP} from "../api/api";
export default {
  data () {
    return {
      form: {
        input: '',
      },
      result: ''
    }
  },
  methods:{
    Submit() {
      FTP(this.form.input).then((res) => {
        console.log(res.data)
        this.result=res.data
      })
    }
  }
}
</script>
<style scoped>
.el-row {
    margin-bottom: 20px;
  }
.el-button{
  width:200px;
  height:50px;
  font-size: 25px;
}
.n{
  margin-left:10px;
  width:95px;
  height:40px;
  font-size: 15px;
}
.n2{
  /* margin-left:200px; */
  width:95px;
  height:40px;
  font-size: 15px;
}
.h1{
  font-size:50px ;
  color: #42b983;
}

ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



